import { useMemo } from 'react';

import ChainIds from '../configs/chainIds';
import { useChainId } from '../packages/web3/hooks';

export default function useNativeToken() {
  const chainId = useChainId();
  return useMemo(() => {
    switch (chainId) {
      case ChainIds.ETH_MAINNET:
        return {
          name: 'ETH',
          symbol: 'ETH',
          decimals: 18,
        };
      case ChainIds.KCC_TESTNET:
      case ChainIds.KCC_MAINNET:
        return {
          name: 'KCS',
          symbol: 'KCS',
          decimals: 18,
        };
      case ChainIds.MATIC_MAINNET:
      case ChainIds.MATIC_TESTNET:
        return {
          name: 'MATIC',
          symbol: 'MATIC',
          decimals: 18,
        };
      default:
        return {
          name: 'BNB',
          symbol: 'BNB',
          decimals: 18,
        };
    }
  }, [chainId]);
}
