import { Route, Switch } from 'react-router-dom';
import { FC } from 'react';
import timezone from 'moment-timezone';

import MainLayout from './layouts/MainLayout';
import CreatePoolPage from './pages/CreatePoolPage';
import CreateTokenPage from './pages/CreateTokenPage';
import PoolDetailPage from './pages/PoolDetailPage';
import EditPoolPage from './pages/EditPoolPage';
import HomePage from './pages/HomePage';
import getPath, { RouteNames } from './utils/path';
import { useWeb3 } from './packages/web3/hooks';
import LeaderboardPage from './pages/LeaderboardPage';
import PoolsPage from './pages/PoolsPage';
import ConnectWalletModal from './packages/web3/components/ConnectWalletModal';

timezone.tz.setDefault('Africa/Abidjan');

type RouterParams = {
  path: string;
  component: FC;
};

function PrivateRoute({ path, component }: RouterParams) {
  const web3 = useWeb3();
  if (!web3) {
    return (
      <div className="py-6 has-text-centered is-size-5">
        <strong>Connect wallet to continue</strong>
      </div>
    );
  }
  return <Route path={path} exact component={component} />;
}

function App() {
  return (
    <MainLayout>
      <Switch>
        <Route path={getPath(RouteNames.Home)} exact component={HomePage} />
        <Route path={getPath(RouteNames.Leaderboard)} exact component={LeaderboardPage} />
        <PrivateRoute path={getPath(RouteNames.CreateLauchpad)} component={CreatePoolPage} />
        <PrivateRoute path={getPath(RouteNames.EditLauchpad)} component={EditPoolPage} />
        <PrivateRoute path={getPath(RouteNames.LauchpadDetail)} component={PoolDetailPage} />
        <PrivateRoute path={getPath(RouteNames.LauchpadList)} component={PoolsPage} />
        <PrivateRoute path={getPath(RouteNames.CreateToken)} component={CreateTokenPage} />
      </Switch>
      <ConnectWalletModal />
    </MainLayout>
  );
}

export default App;
