import { useEffect, useState } from 'react';

import { useWeb3 } from '../packages/web3/hooks';
import { usePoolV2Contract } from './useContract';

let timerUpdateId: any = null;

export default function useUserWhitelist(address: string, index: number, itemPerPage: number, forceUpdate = 1) {
  const [updateId, setUpdateId] = useState<number>(0);
  const [data, setData] = useState<string[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const web3 = useWeb3();
  const contract = usePoolV2Contract(address, web3);

  useEffect(() => {
    (async () => {
      setLoading(true);
      try {
        const result = await contract.methods.getWhitelistedUsers(index, index - 1 + itemPerPage).call();
        const reversed = [...result];
        reversed.reverse();
        setData(reversed);
      } catch (e) {
        //
      } finally {
        setLoading(false);
      }
    })();
  }, [contract, index, itemPerPage, updateId]);

  useEffect(() => {
    clearInterval(timerUpdateId);
    timerUpdateId = setInterval(() => {
      setUpdateId((prev) => prev + 1);
    }, 2500);

    return () => {
      clearInterval(timerUpdateId);
    };
  }, [forceUpdate]);

  useEffect(() => {
    setUpdateId((prev) => prev + 1);
  }, [forceUpdate]);

  return {
    data,
    loading,
  };
}
