import classNames from 'classnames';
import { FC, memo, ReactNode } from 'react';

import FormError from './FormError';
import FormInfo from './FormInfo';

type Props = {
  name: string;
  label: string;
  value: string | number | undefined;
  inputType?: string;
  placeholder?: string;
  isRequired?: boolean;
  touched?: boolean;
  error?: string;
  textInfo?: string;

  handleChange?(e: any): void;
  handleBlur?(e: any): void;
  renderInput?(): ReactNode;
};

const FormField: FC<Props> = ({
  name,
  label,
  value,
  touched,
  placeholder,
  error,
  isRequired = false,
  inputType = 'text',
  textInfo,

  handleBlur,
  handleChange,
  renderInput,
}) => {
  return (
    <div className="field">
      <label className="label" htmlFor={name}>
        {label}
        {isRequired && <sup className="has-text-danger">*</sup>}
      </label>
      <div className="control">
        {renderInput ? (
          renderInput()
        ) : (
          <input
            className={classNames('input', touched && error ? 'is-danger' : '')}
            type={inputType}
            placeholder={placeholder}
            id={name}
            name={name}
            onChange={handleChange}
            onBlur={handleBlur}
            value={value}
          />
        )}
        <FormError touched={Boolean(touched && error)} error={error} />
        {textInfo ? <FormInfo text={textInfo} /> : null}
      </div>
    </div>
  );
};

export default memo(FormField);
