import { FC, memo } from 'react';
import Web3 from 'web3';

import useNativeToken from '../../hooks/useNativeToken';

type Props = {
  softCap?: string;
  hardCap?: string;
};

const PoolItemCapacity: FC<Props> = ({ hardCap, softCap }) => {
  const { symbol } = useNativeToken();
  return (
    <div className={'soft-hard-cap'}>
      <p>Soft/Hard Cap:</p>
      <span>
        {Web3.utils.fromWei(softCap || '0')} {symbol} - {Web3.utils.fromWei(hardCap || '0')} {symbol}
      </span>
    </div>
  );
};

export default memo(PoolItemCapacity);
