import axios from 'axios';
import configs from '../configs';

async function getBlacklistPools() {
  try {
    const { data } = await axios.get('/blacklist.json');
    return data;
  } catch (e) {
    return [];
  }
}

async function viewPool(chainId: number, poolAddress: string) {
  try {
    const { data } = await axios.post(`${configs.API_BASE_URL}/view`, {
      chain_id: chainId,
      pool_address: poolAddress,
    });
    return data;
  } catch (e) {
    return null;
  }
}

async function getLeaderboard(chanId: number, miliseconds?: number) {
  try {
    return (await axios.get(`${configs.API_BASE_URL}/leaderboard/list?date=${miliseconds || ''}&chainId=${chanId}`))
      .data;
  } catch (e) {
    return undefined;
  }
}

async function getBlacklistLeaderboard() {
  try {
    const { data } = await axios.get('/leaderboard_blacklist.json');
    return data;
  } catch (e) {
    return [];
  }
}

const PoolService = {
  getBlacklistPools,
  getBlacklistLeaderboard,
  getLeaderboard,
  viewPool,
};

export default PoolService;
