import { createGlobalState } from 'react-use';
import { useEffect } from 'react';

import AppService from '../services';
import { useChainId } from '../packages/web3/hooks';

type TState = {
  blacklist: Array<{
    address: string;
    chainId: number;
  }>;
};

const shared = createGlobalState<TState>({
  blacklist: [],
});

export function useFetchLeaderboardBlacklist() {
  const [, setBlacklist] = shared();
  useEffect(() => {
    AppService.pool
      .getBlacklistLeaderboard()
      .then((data) => {
        if (Array.isArray(data)) {
          setBlacklist({
            blacklist: data,
          });
        }
      })
      .catch((e) => e);
  }, [setBlacklist]);
}

export default function useLeaderboardBlacklist() {
  const chainId = useChainId();
  const [state] = shared();
  if (state.blacklist.length === 0) return null;
  return state.blacklist.filter((p) => p.chainId === chainId).map((p) => p.address);
}
