import { useEffect, useState } from 'react';

import multicall from '../utils/multicall';
import { useChainId, useWeb3 } from '../packages/web3/hooks';
import { useTokenManagerContract } from './useContract';

const ERC20Abi = require('../abi/ERC20Abi.json');

type TToken = {
  name: string;
  decimals: string;
  symbol: string;
  totalSupply: string;
  isTokenGenerated: boolean;
  poolBalance?: string;
};

export default function useTokenInfo(address?: string) {
  const [tokenInfo, setTokenInfo] = useState<TToken>();
  const [loading, setLoading] = useState<boolean>(false);
  const web3 = useWeb3();
  const tokenManagerContract = useTokenManagerContract(web3);
  const chainId = useChainId();

  useEffect(() => {
    (async () => {
      if (!address || !tokenManagerContract) return;
      try {
        setLoading(true);
        const data: any = {};
        const calls = ['name', 'symbol', 'decimals', 'totalSupply'].map((method) => ({
          address: address,
          name: method,
        }));
        const [[name], [symbol], [decimals], [totalSupply]] = await multicall(
          ERC20Abi,
          calls,
          {
            web3,
          },
          chainId,
        );
        data.name = name;
        data.symbol = symbol;
        data.decimals = decimals.toString();
        data.totalSupply = totalSupply.toString();
        const isTokenGenerated = await tokenManagerContract.methods.isTokenGenerated(address).call();
        data.isTokenGenerated = isTokenGenerated;
        // if (poolAddress) {
        //   const [[balanceOf]] = await multicall(
        //     ERC20Abi,
        //     [
        //       {
        //         address: address,
        //         name: "balanceOf",
        //         params: [poolAddress],
        //       },
        //     ],
        //     {
        //       web3,
        //     },
        //     chainId
        //   );
        //   data.poolBalance = balanceOf.toString();
        // }
        setTokenInfo(data);
      } catch (e) {
        console.log(e);
      } finally {
        setLoading(false);
      }
    })();
  }, [web3, address, chainId, tokenManagerContract]);

  return {
    token: tokenInfo,
    loading,
  };
}
