import { createGlobalState } from 'react-use';
import IPoolInfo from '../types/IPoolInfo';

interface IPool extends IPoolInfo {
  tokenInfo?: {
    name: string;
    decimals: string;
    symbol: string;
    totalSupply: string;
    isTokenGenerated: boolean;
  };
}

type TState = {
  [address: string]: Partial<IPool>;
};

const shared = createGlobalState<TState>({});

export default function usePoolState() {
  return shared();
}
