import { FC, memo } from 'react';
import { Collapse, message } from 'antd';
import { useState } from 'react';
import { Pagination, Spin, Typography } from 'antd';
import { AiOutlineClose } from 'react-icons/ai';

import useUserWhitelist from '../../hooks/useUserWhitelist';
import { usePoolV2Contract } from '../../hooks/useContract';
import { useWeb3 } from '../../packages/web3/hooks';
import { useCallback } from 'react';
import { useWeb3React } from '@web3-react/core';

type Props = {
  numberOfWhitelistedUsers: number;
  address: string;
  isOwner?: boolean;
};

const { Panel } = Collapse;
const ITEM_PER_PAGE = 10;

const WhitelistTable: FC<Props> = ({ address, numberOfWhitelistedUsers, isOwner = false }) => {
  const [page, setPage] = useState<number>(1);
  const [updateId, setUpdateId] = useState<number>(1);
  const web3 = useWeb3();
  const { account } = useWeb3React();
  const { data: users, loading } = useUserWhitelist(address, (page - 1) * ITEM_PER_PAGE, ITEM_PER_PAGE, updateId);
  const contract = usePoolV2Contract(address, web3);
  const onPageChange = (page: number) => {
    setPage(page);
  };
  const deleteUser = useCallback(
    (user: string) => {
      (async () => {
        try {
          await contract.methods.removeWhitelistedUsers([user]).send({
            from: account,
          });
          message.success('User has been deleted from white list');
          setUpdateId((prev) => prev + 1);
        } catch (e) {
          message.error(e?.message || e.toString());
        }
      })();
    },
    [contract, account],
  );
  return (
    <Collapse ghost expandIconPosition="right" activeKey="1" expandIcon={() => null}>
      <Panel header="User whitelist" key="1">
        <Spin spinning={loading && numberOfWhitelistedUsers === 0}>
          <table className="has-text-centered">
            <thead>
              <tr>
                <th>No.</th>
                <th>Address ({numberOfWhitelistedUsers})</th>
                {isOwner && <th>Action</th>}
              </tr>
            </thead>
            <tbody>
              {users.map((addr, index) => (
                <tr key={addr}>
                  <td>{(page - 1) * ITEM_PER_PAGE + index + 1}</td>
                  <td>
                    <Typography.Paragraph copyable>{addr}</Typography.Paragraph>
                  </td>
                  {isOwner && (
                    <td>
                      <span onClick={() => deleteUser(addr)}>
                        <AiOutlineClose />
                      </span>
                    </td>
                  )}
                </tr>
              ))}
            </tbody>
          </table>
        </Spin>
        <div className="mt-4 has-text-centered">
          <Pagination
            defaultCurrent={1}
            pageSize={ITEM_PER_PAGE}
            total={numberOfWhitelistedUsers}
            showSizeChanger={false}
            onChange={onPageChange}
          />
        </div>
      </Panel>
    </Collapse>
  );
};

export default memo(WhitelistTable);
