import Web3 from 'web3';
import { formatUnits } from '@ethersproject/units';
import ITrustScore from '../types/ITrustScore';
import { etherJsFormat } from '../utils/helper';

interface AdditionalInfo {
  name: string;
  logo: string;
  description?: string;
  website?: string;
  twitter?: string;
  github?: string;
  telegram?: string;
}

interface Params {
  isTokenMintByPinksale: boolean;
  refundType?: string;
  maxBuy?: string;
  hardcap?: string;
  listingRate?: string;
  presaleRate?: string;
  liquidityPercentage?: string;
  liquidityLockupTime?: string;
  totalVestingToken?: string;
  totalToken?: string;
  vestingTime?: string;
  additionalInfo: AdditionalInfo;
  nativeToken?: string;
  nativeTokenDecimals?: number;
  tokenSymbol?: string;
  tokenDecimals?: number;
}

// @ts-ignore
const BN = Web3.utils.BN;

class TrustScore {
  private isTokenMintByPinksale: boolean;
  private refundType?: string;
  private maxBuy?: string;
  private hardcap?: string;
  private listingRate?: string;
  private presaleRate?: string;
  private liquidityPercentage?: string;
  private liquidityLockupTime?: string;
  private totalVestingToken?: string;
  private vestingTime?: string;
  private totalToken?: string;
  private additionalInfo: AdditionalInfo;
  private nativeToken?: string;
  private nativeTokenDecimals?: number;
  private tokenSymbol?: string;
  private tokenDecimals?: number;
  private readonly MAX_NAME_SCORE = 2;
  private readonly MAX_LOGO_SCORE = 2;
  private readonly MAX_DESCRIPTION_SCORE = 2;
  private readonly MAX_WEBSITE_SCORE = 1;
  private readonly MAX_TWITTER_SCORE = 1;
  private readonly MAX_GITHUB_SCORE = 1;
  private readonly MAX_TELEGRAM_SCORE = 1;
  private readonly MAX_REFUND_TYPE_SCORE = 10;
  private readonly MAX_BUY_SCORE = 5;
  private readonly MAX_LISTING_RATE_SCORE = 10;
  private readonly MAX_LIQUIDITY_PERCENT_SCORE = 10;
  private readonly MAX_LIQUIDITY_LOCKUP_TIME_SCORE = 5;
  private readonly MAX_TOTAL_VESTING_SCORE = 20;
  private readonly MAX_VESTING_TIME_SCORE = 20;
  private readonly MAX_TOKEN_MINT_BY_PINKSALE_SCORE = 10;

  constructor(params: Params) {
    this.isTokenMintByPinksale = params.isTokenMintByPinksale;
    this.refundType = params.refundType;
    this.maxBuy = params.maxBuy;
    this.hardcap = params.hardcap;
    this.listingRate = params.listingRate;
    this.presaleRate = params.presaleRate;
    this.liquidityPercentage = params.liquidityPercentage;
    this.liquidityLockupTime = params.liquidityLockupTime;
    this.totalVestingToken = params.totalVestingToken;
    this.totalToken = params.totalToken;
    this.vestingTime = params.vestingTime;
    this.additionalInfo = params.additionalInfo;
    this.nativeToken = params.nativeToken;
    this.nativeTokenDecimals = params.nativeTokenDecimals;
    this.tokenSymbol = params.tokenSymbol;
    this.tokenDecimals = params.tokenDecimals;
  }

  private getNameScore(): number {
    return this.additionalInfo.name ? this.MAX_NAME_SCORE : 0;
  }

  private getLogoScore(): number {
    return this.additionalInfo.logo ? this.MAX_LOGO_SCORE : 0;
  }

  private getDescriptionScore(): number {
    return this.additionalInfo.description ? this.MAX_DESCRIPTION_SCORE : 0;
  }

  private getWebsiteScore(): number {
    return this.additionalInfo.website ? this.MAX_WEBSITE_SCORE : 0;
  }

  private getTwitterScore(): number {
    return this.additionalInfo.twitter ? this.MAX_TWITTER_SCORE : 0;
  }

  private getGithubScore(): number {
    return this.additionalInfo.github ? this.MAX_GITHUB_SCORE : 0;
  }

  private getTelegramScore(): number {
    return this.additionalInfo.telegram ? this.MAX_TELEGRAM_SCORE : 0;
  }

  private getAdditionalInfoScore(): number {
    return (
      this.getNameScore() +
      this.getLogoScore() +
      this.getDescriptionScore() +
      this.getWebsiteScore() +
      this.getTwitterScore() +
      this.getGithubScore() +
      this.getTelegramScore()
    );
  }

  private getRefundTypeScore(): number {
    return Number(this.refundType) === 1 ? this.MAX_REFUND_TYPE_SCORE : 0;
  }

  private getTokenMintByPinksaleScore(): number {
    return this.isTokenMintByPinksale ? this.MAX_TOKEN_MINT_BY_PINKSALE_SCORE : 0;
  }

  private getTokenScore(): number {
    return this.getTokenMintByPinksaleScore() + this.getRefundTypeScore();
  }

  private getMaxBuyScore(): number {
    if (!this.maxBuy || !this.hardcap || Number(this.hardcap) === 0) return 0;
    const percentage = new BN(this.maxBuy).div(new BN(this.hardcap)).mul(new BN(100)).toNumber();
    if (percentage > 5) return 0;
    if (percentage <= 0.5) return this.MAX_BUY_SCORE;
    return 5 - percentage;
  }

  private getListingRateScore(): [number, number] {
    if (!this.listingRate || !this.presaleRate || Number(this.presaleRate) === 0 || !this.tokenDecimals) return [0, 0];
    const listingRate = Number(formatUnits(this.listingRate, this.tokenDecimals));
    const presaleRate = Number(formatUnits(this.presaleRate, this.tokenDecimals));
    const percentage = (listingRate / presaleRate) * 100;
    const diff = 100 - percentage;
    const MAX_PERCENT = 30;
    const ratio = this.MAX_LISTING_RATE_SCORE / MAX_PERCENT;
    if (diff > MAX_PERCENT) return [0, diff];
    if (percentage <= 0) return [this.MAX_LISTING_RATE_SCORE, diff];
    return [this.MAX_LISTING_RATE_SCORE - diff * ratio, diff];
  }

  private getLiquidityPercentageScore(): number {
    if (!this.liquidityPercentage) return 0;
    const percentage = Number(this.liquidityPercentage);
    const MAX_PERCENT = 100;
    const MIN_PERCENT = 51;
    const range = MAX_PERCENT - MIN_PERCENT;
    if (percentage <= MIN_PERCENT) return 0;
    if (percentage === MAX_PERCENT) return this.MAX_LIQUIDITY_PERCENT_SCORE;
    return this.MAX_LIQUIDITY_PERCENT_SCORE - ((MAX_PERCENT - percentage) / range) * this.MAX_LIQUIDITY_PERCENT_SCORE;
  }

  private getLiquidityLockupTimeScore(): number {
    if (!this.liquidityLockupTime) return 0;
    const days = Number(this.liquidityLockupTime);
    const MAX_DAYS = 365;
    const MIN_DAYS = 30;
    const range = MAX_DAYS - MIN_DAYS;
    if (days <= MIN_DAYS) return 0;
    if (days >= MAX_DAYS) return this.MAX_LIQUIDITY_LOCKUP_TIME_SCORE;
    return this.MAX_LIQUIDITY_LOCKUP_TIME_SCORE - ((MAX_DAYS - days) / range) * this.MAX_LIQUIDITY_LOCKUP_TIME_SCORE;
  }

  private getSaleScores(): number {
    return (
      this.getMaxBuyScore() +
      this.getListingRateScore()[0] +
      this.getLiquidityPercentageScore() +
      this.getLiquidityLockupTimeScore()
    );
  }

  private getTotalVestingTokenScore(): [number, number] {
    if (
      !this.totalVestingToken ||
      Number(this.totalVestingToken) === 0 ||
      !this.totalToken ||
      Number(this.totalToken) === 0
    )
      return [0, 0];
    const totalTokens = Number(formatUnits(this.totalToken, this.tokenDecimals));
    const totalLockedTokens = Number(formatUnits(this.totalVestingToken, this.tokenDecimals));
    const totalUnlockedTokens = totalTokens - totalLockedTokens;
    const percentage = (totalUnlockedTokens / totalTokens) * 100;
    const MAX_PERCENT = 10;
    const ratio = MAX_PERCENT / this.MAX_TOTAL_VESTING_SCORE;
    if (percentage >= MAX_PERCENT) return [0, totalUnlockedTokens < 0 ? 0 : totalUnlockedTokens];
    if (percentage <= 0) return [this.MAX_TOTAL_VESTING_SCORE, totalUnlockedTokens < 0 ? 0 : totalUnlockedTokens];
    return [this.MAX_TOTAL_VESTING_SCORE - percentage / ratio, totalUnlockedTokens < 0 ? 0 : totalUnlockedTokens];
  }

  private getVestingTimeScore(): number {
    if (!this.vestingTime) return 0;
    const days = Number(this.vestingTime);
    const MAX_DAYS = 365;
    const MIN_DAYS = 90;
    const range = MAX_DAYS - MIN_DAYS;
    if (days <= MIN_DAYS) return 0;
    if (days >= MAX_DAYS) return this.MAX_VESTING_TIME_SCORE;
    return this.MAX_VESTING_TIME_SCORE - ((MAX_DAYS - days) / range) * this.MAX_VESTING_TIME_SCORE;
  }

  private getVestingScores(): number {
    return this.getTotalVestingTokenScore()[0] + this.getVestingTimeScore();
  }

  public getTotalScore(): number {
    return this.getTokenScore() + this.getVestingScores() + this.getSaleScores() + this.getAdditionalInfoScore();
  }

  public scores(): ITrustScore {
    return {
      standards: [
        {
          name: 'Max buy',
          value: [
            [
              'Max buy',
              this.nativeToken
                ? `${etherJsFormat(this.maxBuy || '0', this.nativeTokenDecimals)} ${this.nativeToken}`
                : this.maxBuy || '0',
            ],
            [
              'HardCap',
              this.nativeToken
                ? `${etherJsFormat(this.hardcap || '0', this.nativeTokenDecimals)} ${this.nativeToken}`
                : this.hardcap || '0',
            ],
          ],
          score: this.getMaxBuyScore(),
          max: this.MAX_BUY_SCORE,
          reason: 'The max buy should be less than 5% of hardcap, 0% is the max score',
        },
        {
          name: 'Listing rate',
          value: [
            [
              'Listing rate',
              this.tokenSymbol
                ? `${etherJsFormat(this.listingRate || '0', this.tokenDecimals)} ${this.tokenSymbol}`
                : this.listingRate || '0',
            ],
            [
              'Presale rate',
              this.tokenSymbol
                ? `${etherJsFormat(this.presaleRate || '0', this.tokenDecimals)} ${this.tokenSymbol}`
                : this.presaleRate || '0',
            ],
            ['Diff', `${this.getListingRateScore()[1]}%`],
          ],
          score: this.getListingRateScore()[0],
          max: this.MAX_LISTING_RATE_SCORE,
          reason: 'The listing rate should be less than 30% of presale rate, 0% is the max score',
        },
        {
          name: 'Liquidity percentage',
          value: `${this.liquidityPercentage}%`,
          score: this.getLiquidityPercentageScore(),
          max: this.MAX_LIQUIDITY_PERCENT_SCORE,
          reason: 'The liquidity percentage should be between 51% and 100%, higher is better',
        },
        {
          name: 'Liquidity lockup time',
          value: this.liquidityLockupTime,
          score: this.getLiquidityLockupTimeScore(),
          max: this.MAX_LIQUIDITY_LOCKUP_TIME_SCORE,
          reason: 'The liquidity lockup time should be between 30 and 365 days, higher is better',
        },
        {
          name: 'Refund type',
          value: Number(this.refundType) === 1 ? 'Burn' : 'Refund',
          score: this.getRefundTypeScore(),
          max: this.MAX_REFUND_TYPE_SCORE,
          reason: 'The refund type should be "burn"',
        },
        {
          name: 'Name',
          value: this.additionalInfo.name,
          score: this.getNameScore(),
          max: this.MAX_NAME_SCORE,
          reason: 'The name is mandatory',
        },
        {
          name: 'Description',
          value: this.additionalInfo.description,
          score: this.getDescriptionScore(),
          max: this.MAX_DESCRIPTION_SCORE,
          reason: 'The description should be provided, for users to know more about your project',
        },
        {
          name: 'Token mint by pinksale',
          value: this.isTokenMintByPinksale,
          score: this.getTokenMintByPinksaleScore(),
          max: this.MAX_TOKEN_MINT_BY_PINKSALE_SCORE,
          reason: 'The token should be minted by pinksale',
        },
        {
          name: 'Logo',
          value: this.additionalInfo.logo,
          score: this.getLogoScore(),
          max: this.MAX_LOGO_SCORE,
          reason: 'The logo should be provided , for users to easily recognize your project',
        },
        {
          name: 'Website',
          value: this.additionalInfo.website,
          score: this.getWebsiteScore(),
          max: this.MAX_WEBSITE_SCORE,
          reason: 'Website should be provided, for increased levels of user trust',
        },
        {
          name: 'Twitter',
          value: this.additionalInfo.twitter,
          score: this.getTwitterScore(),
          max: this.MAX_TWITTER_SCORE,
          reason: 'Twitter should be provided, for increased levels of user trust',
        },
        {
          name: 'Github',
          value: this.additionalInfo.github,
          score: this.getGithubScore(),
          max: this.MAX_GITHUB_SCORE,
          reason: 'Github repositories should be provided, for increased levels of user trust',
        },
        {
          name: 'Telegram',
          value: this.additionalInfo.telegram,
          score: this.getTelegramScore(),
          max: this.MAX_TELEGRAM_SCORE,
          reason: 'Telegram channel should be provided to increase trusted value',
        },
        {
          name: 'Unvested tokens',
          value: [
            [
              'Total supply',
              this.tokenSymbol
                ? `${etherJsFormat(this.totalToken || '0', this.tokenDecimals)} ${this.tokenSymbol}`
                : this.totalToken || '0',
            ],
            [
              'Total locked tokens',
              this.tokenSymbol
                ? `${etherJsFormat(this.totalVestingToken || '0', this.tokenDecimals)} ${this.tokenSymbol}`
                : this.totalVestingToken || '0',
            ],
            [
              'Total unlocked tokens',
              this.tokenSymbol ? `${this.getTotalVestingTokenScore()[1]} ${this.tokenSymbol}` : '0',
            ],
          ],
          score: this.getTotalVestingTokenScore()[0],
          max: this.MAX_TOTAL_VESTING_SCORE,
          reason: 'Total unlocked tokens amount should be zero for anti-rug setup',
        },
        {
          name: 'Vesting time',
          value: this.vestingTime,
          score: this.getVestingTimeScore(),
          max: this.MAX_VESTING_TIME_SCORE,
          reason: 'Vesting time should be between 90 and 365 days, higher is better',
        },
      ],
      vestingScores: this.getVestingScores(),
      tokenScores: this.getTokenScore(),
      saleScores: this.getSaleScores(),
      additionalScores: this.getAdditionalInfoScore(),
      total: this.getTotalScore(),
    };
  }
}

export default TrustScore;
