import { FC } from 'react';
import { Link } from 'react-router-dom';
import { AiOutlinePlus, AiOutlineMenuFold, AiOutlineMenu } from 'react-icons/ai';
import { Menu, Dropdown } from 'antd';

import getPath, { RouteNames } from '../../utils/path';
import ConnectWalletButton from './ConnectWalletButton';
import Account from './Account';
import Logo from '../../assets/images/pinkswap.png';
import useSidebar from '../../hooks/useSidebar';
import configs from '../../configs';
import SwitchNetwork from './SwitchNetwork';

const menu = (
  <Menu>
    <Menu.Item key="launchpad">
      <Link to={getPath(RouteNames.CreateLauchpad)} className="dropdown-item">
        Launchpad
      </Link>
    </Menu.Item>
    <Menu.Item key="token">
      <Link to={getPath(RouteNames.CreateToken)} className="dropdown-item">
        Token
      </Link>
    </Menu.Item>
    <Menu.Item key="pinklock">
      <a target="_blank" rel="noreferrer noopener" href={`${configs.links.PINKLOCK}/locker`} className="dropdown-item">
        PinkLock
      </a>
    </Menu.Item>
  </Menu>
);

const HeaderNavigation: FC = () => {
  const [sidebar, setSidebar] = useSidebar();
  const toggle = () => setSidebar((prev) => ({ collapsed: !prev.collapsed }));
  return (
    <nav className="flex items-center noselect">
      <div className="pr-2 pt-2 is-size-4" style={{ cursor: 'pointer' }} onClick={toggle}>
        {sidebar.collapsed ? <AiOutlineMenu /> : <AiOutlineMenuFold />}
      </div>
      <Link className="logo mr-3" to={getPath(RouteNames.Home)}>
        <div className="logo-icon">
          <img src={Logo} alt="pinksale" />
        </div>
        <div className="logo-text pt-1 hide-on-mobile">PinkSale</div>
      </Link>
      <div className="flex-1"></div>
      <Dropdown overlay={menu}>
        <div className="connectButton on-mobile">
          <AiOutlinePlus />
          <span className="ml-2 hide-on-mobile">Create</span>
        </div>
      </Dropdown>
      <SwitchNetwork />
      <Account />
      <ConnectWalletButton />
    </nav>
  );
};

export default HeaderNavigation;
