import { useEffect, useState } from 'react';
import ChainIds from '../configs/chainIds';
import { useChainId } from '../packages/web3/hooks';

export default function useExplorerDomain() {
  const [link, setLink] = useState<string>();
  const chainId = useChainId();

  useEffect(() => {
    switch (chainId) {
      case ChainIds.BSC_TESTNET:
        setLink('https://testnet.bscscan.com');
        break;
      case ChainIds.BSC_MAINNET:
        setLink('https://bscscan.com');
        break;
      case ChainIds.MATIC_TESTNET:
        setLink('https://mumbai.polygonscan.com');
        break;
      case ChainIds.MATIC_MAINNET:
        setLink('https://polygonscan.com');
        break;
      case ChainIds.ETH_MAINNET:
        setLink('https://etherscan.io');
        break;
      case ChainIds.KCC_MAINNET:
        setLink('https://explorer.kcc.io/en');
        break;
      // case ChainIds.RINKEBY:
      //   setLink('https://rinkeby.etherscan.io');
      //   break;
      // case ChainIds.KOVAN:
      //   setLink('https://kovan.etherscan.io');
      //   break;
      // case ChainIds.GOERLI:
      //   setLink('https://goerli.etherscan.io');
      //   break;
      // case ChainIds.ROPSTEN:
      //   setLink('https://ropsten.etherscan.io');
      //   break;
      default:
        setLink('https://bscscan.com');
        break;
    }
  }, [chainId]);

  return link;
}
