import { useMemo } from 'react';
import Web3 from 'web3';
import configs from '../configs';
import ChainIds from '../configs/chainIds';
import { useChainId } from '../packages/web3/hooks';
import { getContract } from '../utils/helper';

const poolAbi = require('../abi/PoolContract.json');
const poolAbiV2 = require('../abi/PoolContractV3.json'); // change to v3
const erc20Abi = require('../abi/ERC20Abi.json');
const uniswapRouterV2Abi = require('../abi/UniswapV2Router.json');
const uniswapFactoryAbi = require('../abi/UniswapFactoryAbi.json');
const excludeAbi = require('../abi/CheckExcludeAbi.json');
const babyTokenAbi = require('../abi/BabyTokenAbi.json');

export function usePoolContract(address?: string, web3?: any): any {
  return useMemo(() => getContract(poolAbi, address || '', web3), [address, web3]);
}

export function getBabyTokenContract(address?: string, web3?: any): any {
  return getContract(babyTokenAbi, address || '', web3);
}

export function getUniswapV2RouterContract(routerAddress?: string, web3?: any): any {
  return getContract(uniswapRouterV2Abi, routerAddress || '', web3);
}

export function getUniswapFactoryContract(factoryAddress?: string, web3?: any): any {
  return getContract(uniswapFactoryAbi, factoryAddress || '', web3);
}

export function usePoolV2Contract(address?: string, web3?: any): any {
  return useMemo(() => getContract(poolAbiV2, address || '', web3), [address, web3]);
}

export function useBnbBusdContract() {
  return useMemo(
    () => getContract(configs.contract.wbnbBusdPair.bsc.ABI, configs.contract.wbnbBusdPair.bsc.ADDRESS),
    [],
  );
}

export function usePoolFactoryContract(web3?: any) {
  const chainId = useChainId();
  return useMemo(() => {
    switch (chainId) {
      case ChainIds.ETH_MAINNET:
        return getContract(configs.contract.factory.ABI, configs.contract.factory.addresses.ETH, web3);
      case ChainIds.BSC_TESTNET:
        return getContract(configs.contract.factory.ABI, configs.contract.factory.addresses.BSC_TESTNET, web3);
      case ChainIds.MATIC_TESTNET:
        return getContract(configs.contract.factory.ABI, configs.contract.factory.addresses.MATIC_TESTNET, web3);
      case ChainIds.MATIC_MAINNET:
        return getContract(configs.contract.factory.ABI, configs.contract.factory.addresses.MATIC, web3);
      case ChainIds.KCC_TESTNET:
        return getContract(configs.contract.factory.ABI, configs.contract.factory.addresses.KCC_TESTNET, web3);
      case ChainIds.KCC_MAINNET:
        return getContract(configs.contract.factory.ABI, configs.contract.factory.addresses.KCC, web3);
      default:
        return getContract(configs.contract.factory.ABI, configs.contract.factory.addresses.BSC, web3);
    }
  }, [web3, chainId]);
}

