import classNames from 'classnames';
import { useFormik } from 'formik';
import { FC, KeyboardEvent, useCallback, useMemo, useState } from 'react';
import { message } from 'antd';
import * as Yup from 'yup';
import Web3 from 'web3';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useWeb3React } from '@web3-react/core';
import { Link } from 'react-router-dom';
import { parseUnits, parseEther } from '@ethersproject/units';
import { useEffect } from 'react';

import Button from '../common/Button';
import configs from '../../configs';
import getPath, { RouteNames } from '../../utils/path';
import useSupportedRouter from '../../hooks/useSupportedRouter';
import useTokenRouter from '../../hooks/useTokenRouter';
import useExplorerDomain from '../../hooks/useExplorerDomain';
import { useChainId, useWeb3 } from '../../packages/web3/hooks';
import {
  getUniswapFactoryContract,
  getUniswapV2RouterContract,
  useBabyTokenContract,
  useLiquidityTokenContract,
  useStandardTokenContract,
} from '../../hooks/useContract';
import useTokenFee from '../../hooks/useTokenFee';
import multicall from '../../utils/multicall';
import { currencyFormat, etherJsFormat } from '../../utils/helper';
import useNativeToken from '../../hooks/useNativeToken';

const Erc20Abi = require('../../abi/ERC20Abi.json');

type TForm = {
  tokenName: string;
  tokenSymbol: string;
  tokenDecimals?: number;
  totalSupply?: string;
  // liquidity token
  charity?: string;
  charityBps?: number;
  taxFeeBps?: number;
  liquidityFeeBps?: number;
  tokenType?: string;
  router?: string;
  maxTxBps?: number;
  // baby token has router,
  rewardToken?: string;
  marketingWallet?: string;
  tokenRewardsFee?: number;
  liquidityFee?: number;
  marketingFee?: number;
  minimumTokenBalanceForDividends?: number;
};

const validationSchema: Yup.SchemaOf<TForm> = Yup.object().shape({
  tokenName: Yup.string().required().min(2).max(255),
  tokenSymbol: Yup.string().required().min(2).max(255),
  tokenDecimals: Yup.number()
    .positive()
    .min(2)
    .max(64)
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard') {
          return value && value > 0;
        }
        return true;
      },
      message: 'Invalid decimals',
    }),
  totalSupply: Yup.string().required(),
  taxFeeBps: Yup.number().positive().max(100).min(0.01),
  liquidityFeeBps: Yup.number().positive().max(100).min(0.01),
  tokenType: Yup.string(),
  router: Yup.string().test({
    test: (value: any, ctx: any) => {
      if (ctx.parent.tokenType === 'standard') return true;
      return value && value !== '';
    },
    message: 'Invalid router',
  }),
  maxTxBps: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard' || ctx.parent.tokenType === 'baby') return true;
        return value && value >= 0.01;
      },
      message: 'Invalid max tx percent',
    }),
  charity: Yup.string()
    .max(255)
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard') return true;
        if (!value) return true;
        return Web3.utils.isAddress(value) && value !== configs.ZERO_ADDRESS;
      },
      message: 'Address is invalid',
    }),
  charityBps: Yup.number()
    .positive()
    .max(100)
    .min(1)
    .when('charity', (c: any) => {
      if (c) {
        return Yup.number().positive().max(100).min(1).required();
      }
      return Yup.number().positive().max(100).min(1);
    }),
  rewardToken: Yup.string().test({
    test: (value: any, ctx: any) => {
      if (ctx.parent.tokenType === 'standard' || ctx.parent.tokenType === 'liquidity') return true;
      if (!value) return true;
      return Web3.utils.isAddress(value) && value !== configs.ZERO_ADDRESS;
    },
    message: 'Address is invalid',
  }),
  tokenRewardsFee: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number().positive().max(100).min(0.01).required('Token reward fee is a required field');
      }
      return Yup.number().positive().max(100).min(0.01);
    }),
  liquidityFee: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number().positive().max(100).min(0.01).required('Auto add liquidity is a required field');
      }
      return Yup.number().positive().max(100).min(0.01);
    }),
  marketingWallet: Yup.string().test({
    test: (value: any, ctx: any) => {
      if (ctx.parent.tokenType === 'standard' || ctx.parent.tokenType === 'liquidity') return true;
      if (!value) return true;
      return Web3.utils.isAddress(value) && value !== configs.ZERO_ADDRESS;
    },
    message: 'Address is invalid',
  }),
  marketingFee: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number().positive().max(100).min(0.01).required('Marketing fee is a required field');
      }
      return Yup.number().positive().max(100).min(0.01);
    }),
  minimumTokenBalanceForDividends: Yup.number()
    .positive()
    .min(1)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number().positive().min(1).required('Minimum token balance for dividends is a required field');
      }
      return Yup.number().positive().min(1);
    }),
});

