import { useEffect } from 'react';
import { useState } from 'react';

import { useWeb3 } from '../packages/web3/hooks';
import { useBabyTokenContract, useLiquidityTokenContract, useStandardTokenContract } from './useContract';

export default function useTokenFee(type = 'standard') {
  const web3 = useWeb3();
  const standardContract = useStandardTokenContract(web3);
  const liquidityContract = useLiquidityTokenContract(web3);
  const babyContract = useBabyTokenContract(web3);
  const [fee, setFee] = useState<string>();
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    if (!standardContract) return;
    (async () => {
      setLoading(true);
      try {
        if (type === 'standard') {
          const f = await standardContract.methods.flatFee().call();
          setFee(f);
        } else if (type === 'liquidity') {
          const f = await liquidityContract.methods.flatFee().call();
          setFee(f);
        } else {
          const f = await babyContract.methods.flatFee().call();
          setFee(f);
        }
      } catch (e) {
        // console.log(e);
      } finally {
        setLoading(false);
      }
    })();
  }, [standardContract, type, babyContract, liquidityContract]);
  return { fee, loading };
}
