import Web3 from 'web3';
import configs from '../configs';
import IPoolInfo from '../types/IPoolInfo';
import multicall from './multicall';
import { decodePoolDetailData } from './helper';

const ERC20Abi = require('../abi/ERC20Abi.json');

export async function getPool(address: string, chainId: number | undefined, web3: any, factoryContract: any) {
  const data: Partial<IPoolInfo> = {};
  try {
    const [[version]] = await multicall(configs.contract.pool.ABI, [{ address, name: 'version' }], { web3 }, chainId);
    let abi = configs.contract.pool.ABI;
    const methods = [
      'endTime',
      'factory',
      'hardCap',
      'liquidityListingRate',
      'liquidityPercent',
      'liquidityUnlockTime',
      'maxContribution',
      'minContribution',
      'owner',
      'rate',
      'refundType',
      'router',
      'softCap',
      'startTime',
      'token',
      'totalClaimed',
      'totalRaised',
      'totalVolumePurchased',
      'poolDetails',
      'liquidityLockDays',
      'completedKyc',
      'governance',
      'poolState',
      'withdrawableTokens',
      // "withdrawVestingToken",
      'totalVestingTokens',
      'tgeTime',
      'tgeTokensRelease',
      'cycle',
      'tokensReleaseEachCycle',
      'totalVestedTokens',
      'tgeLockDuration',
    ];
    if (version === 2) {
      abi = configs.contract.poolV2.ABI;
      const [[useWhitelisting], [numberOfWhitelistedUsers]] = await multicall(
        abi,
        [
          {
            address,
            name: 'useWhitelisting',
          },
          {
            address,
            name: 'getNumberOfWhitelistedUsers',
          },
        ],
        { web3 },
        chainId,
      );
      data.useWhitelisting = useWhitelisting;
      data.numberOfWhitelistedUsers = numberOfWhitelistedUsers.toString();
    }
    const calls = methods.map((method) => ({
      address,
      name: method,
    }));
    const [
      [endTime],
      [factory],
      [hardCap],
      [liquidityListingRate],
      [liquidityPercent],
      [liquidityUnlockTime],
      [maxContribution],
      [minContribution],
      [owner],
      [rate],
      [refundType],
      [router],
      [softCap],
      [startTime],
      [token],
      [totalClaimed],
      [totalRaised],
      [totalVolumePurchased],
      [poolDetails],
      [liquidityLockDays],
      [completedKyc],
      [governance],
      [poolState],
      [withdrawableTokens],
      // [withdrawVestingToken],
      [totalVestingTokens],
      [tgeTime],
      [tgeTokensRelease],
      [cycle],
      [tokensReleaseEachCycle],
      [totalVestedTokens],
      [tgeLockDuration],
    ] = await multicall(abi, calls, { web3 }, chainId);
    const tokenFeePercent = await factoryContract.methods.tokenFeePercent().call();
    let status: any;
    const now = +new Date() / 1000;
    const start = Number(startTime);
    const end = Number(endTime);
    if (now < start) {
      status = 'incoming';
    } else if (now >= start && now < end) {
      status = 'inprogress';
    } else if (now >= end) {
      status = 'ended';
    }
    const raised = Number(Web3.utils.fromWei(totalRaised.toString()));
    const hcap = Number(Web3.utils.fromWei(hardCap.toString()));
    if (raised >= hcap) {
      status = 'ended';
    }
    if (Number(poolState) === 1) {
      status = 'ended';
    }
    if (Number(poolState) === 2) {
      status = 'canceled';
    }
    return {
      endTime: endTime.toString(),
      factory,
      hardCap: hardCap.toString(),
      liquidityListingRate: liquidityListingRate.toString(),
      liquidityPercent: liquidityPercent.toString(),
      liquidityUnlockTime: liquidityUnlockTime.toString(),
      maxContribution: maxContribution.toString(),
      minContribution: minContribution.toString(),
      owner,
      rate: rate.toString(),
      refundType: refundType.toString(),
      router,
      softCap: softCap.toString(),
      startTime: startTime.toString(),
      token,
      totalClaimed: totalClaimed.toString(),
      totalRaised: totalRaised.toString(),
      totalVolumePurchased: totalVolumePurchased.toString(),
      status,
      poolDetails: decodePoolDetailData(poolDetails),
      tokenFeePercent,
      liquidityLockDays: liquidityLockDays.toString(),
      completedKyc,
      governance,
      poolState: Number(poolState),
      withdrawableTokens: withdrawableTokens.toString(),
      // withdrawVestingToken: withdrawVestingToken.toString(),
      totalVestingTokens: totalVestingTokens.toString(),
      tgeTime: tgeTime.toString(),
      tgeTokensRelease: tgeTokensRelease.toString(),
      cycle: cycle.toString(),
      tokensReleaseEachCycle: tokensReleaseEachCycle.toString(),
      totalVestedTokens: totalVestedTokens.toString(),
      tgeLockDuration: tgeLockDuration.toString(),
      version,
      ...data,
    };
  } catch (e) {
    return undefined;
  }
}

async function getToken(
  address: string,
  contract: any,
  web3: any,
  chainId?: number,
  // poolAddress?: string
) {
  if (!address) return undefined;
  try {
    const data: any = {};
    const calls = ['name', 'symbol', 'decimals', 'totalSupply'].map((method) => ({
      address: address,
      name: method,
    }));
    const [[name], [symbol], [decimals], [totalSupply]] = await multicall(
      ERC20Abi,
      calls,
      {
        web3,
      },
      chainId,
    );
    data.name = name;
    data.symbol = symbol;
    data.decimals = decimals.toString();
    data.totalSupply = totalSupply.toString();
    const isTokenGenerated = await contract.methods.isTokenGenerated(address).call();
    data.isTokenGenerated = isTokenGenerated;
    // if (poolAddress) {
    //   const [[balanceOf]] = await multicall(
    //     ERC20Abi,
    //     [
    //       {
    //         address: address,
    //         name: "balanceOf",
    //         params: [poolAddress],
    //       },
    //     ],
    //     {
    //       web3,
    //     },
    //     chainId
    //   );
    //   data.poolBalance = balanceOf.toString();
    // }
    return data;
  } catch (e) {
    return undefined;
  }
}

export async function getAdditionLoggedInInfo(address?: string, account?: string | null, web3?: any, chainId?: number) {
  if (!address || !account || !web3) return undefined;
  const data: Partial<IPoolInfo> = {};
  try {
    const [[version]] = await multicall(configs.contract.pool.ABI, [{ address, name: 'version' }], { web3 }, chainId);
    const calls = ['claimedOf', 'purchasedOf', 'refundedOf', 'contributionOf'].map((method) => ({
      address,
      name: method,
      params: [account],
    }));
    let abi = configs.contract.pool.ABI;
    if (version === 2) {
      abi = configs.contract.poolV2.ABI;
      const [[isUserWhitelisted]] = await multicall(
        abi,
        [
          {
            address,
            name: 'isUserWhitelisted',
            params: [account],
          },
        ],
        { web3 },
        chainId,
      );
      data.isUserWhitelisted = isUserWhitelisted;
    }
    const [[claimedOf], [purchasedOf], [refundedOf], [contributionOf]] = await multicall(
      configs.contract.pool.ABI,
      calls,
      { web3 },
      chainId,
    );
    return {
      ...data,
      version,
      claimedOf: claimedOf.toString(),
      purchasedOf: purchasedOf.toString(),
      refundedOf: refundedOf.toString(),
      contributionOf: contributionOf.toString(),
    };
  } catch (e) {
    return undefined;
  }
}

export async function poolWithInfo(
  address: string,
  web3: any,
  tokenManagerContract: any,
  factoryContract: any,
  account?: string | null,
  chainId?: number,
) {
  const pool = await getPool(address, chainId, web3, factoryContract);
  const token = await getToken(
    pool?.token,
    tokenManagerContract,
    web3,
    chainId,
    // address,
  );
  const additionalInfo = await getAdditionLoggedInInfo(address, account, web3, chainId);
  return {
    address,
    pool,
    token,
    additionalInfo,
  };
}
