/* eslint-disable react-hooks/exhaustive-deps */
import { FC, useEffect, useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { FiGlobe, FiTwitter, FiGithub } from 'react-icons/fi';
import { RiFacebookCircleLine } from 'react-icons/ri';
import { FaDiscord, FaTelegramPlane } from 'react-icons/fa';
import { AiOutlineReddit, AiOutlineInstagram } from 'react-icons/ai';
import { BiImage } from 'react-icons/bi';
import classNames from 'classnames';
import { message, Card } from 'antd';
import { useHistory, useRouteMatch } from 'react-router';
import { useWeb3React } from '@web3-react/core';

import Button from '../components/common/Button';
import { encodePoolDetailData } from '../utils/helper';
import usePoolInfo from '../hooks/usePoolInfo';
import { useWeb3 } from '../packages/web3/hooks';
import { usePoolContract } from '../hooks/useContract';

type TForm = {
  logoUrl: string;
  website: string;
  facebook?: string;
  twitter?: string;
  github?: string;
  telegram?: string;
  reddit?: string;
  description?: string;
  instagram?: string;
  discord?: string;
};

const validationSchema: Yup.SchemaOf<TForm> = Yup.object()
  .shape({
    logoUrl: Yup.string().url('Invalid URL').required('Logo cannot be blank'),
    website: Yup.string().url('Invalid URL').required('Website cannot be blank'),
    facebook: Yup.string().url('Invalid URL').max(512, 'URL must be 512 characters or less'),
    telegram: Yup.string().url('Invalid URL').max(512, 'URL must be 512 characters or less'),
    twitter: Yup.string().url('Invalid URL').max(512, 'URL must be 512 characters or less'),
    reddit: Yup.string().url('Invalid URL').max(512, 'URL must be 512 characters or less'),
    github: Yup.string().url('Invalid URL').max(512, 'URL must be 512 characters or less'),
    instagram: Yup.string().url('Invalid URL').max(512, 'URL must be 512 characters or less'),
    discord: Yup.string().url('Invalid URL').max(512, 'URL must be 512 characters or less'),
    description: Yup.string()
      .min(128, 'Description must be 128 characters or more')
      .max(512, 'Description must be 512 characters or less'),
  })
  .defined();

const EditPoolPage: FC = () => {
  const [loading, setLoading] = useState<boolean>(false);
  const router = useRouteMatch<{
    id: string;
  }>();
  const history = useHistory();
  const web3 = useWeb3();
  const contract = usePoolContract(router.params.id, web3);
  const { account } = useWeb3React();
  const { pool } = usePoolInfo(router.params.id);
  const poolDetails = pool?.poolDetails;
  const updatePool = async (detail: string) => {
    setLoading(true);
    try {
      await contract.methods.updatePoolDetails(detail).send({
        from: account,
      });
      message.success('Update pool success');
      history.goBack();
    } catch (e) {
      if (e && e.message) {
        message.error(e.message);
      }
    } finally {
      setLoading(false);
    }
  };
  const formik = useFormik<TForm>({
    initialValues: {
      logoUrl: '',
      website: '',
    },
    validationSchema,
    validateOnMount: true,
    onSubmit: (values: TForm) => {
      if (!web3) return;
      try {
        updatePool(
          encodePoolDetailData({
            logoUrl: values.logoUrl,
            website: values.website,
            facebook: values.facebook,
            twitter: values.twitter,
            telegram: values.telegram,
            reddit: values.reddit,
            description: values.description,
            github: values.github,
            instagram: values.instagram,
            discord: values.discord,
          }),
        );
      } catch (e) {
        //
      }
    },
  });

  const goBack = () => history.goBack();

  useEffect(() => {
    if (!poolDetails) return;
    formik.setFieldValue('logoUrl', poolDetails.logoUrl);
    formik.setFieldValue('website', poolDetails.website);
    if (poolDetails.facebook) {
      formik.setFieldValue('facebook', poolDetails.facebook);
    }
    if (poolDetails.twitter) {
      formik.setFieldValue('twitter', poolDetails.twitter);
    }
    if (poolDetails.telegram) {
      formik.setFieldValue('telegram', poolDetails.telegram);
    }
    if (poolDetails.github) {
      formik.setFieldValue('github', poolDetails.github);
    }
    if (poolDetails.reddit) {
      formik.setFieldValue('reddit', poolDetails.reddit);
    }
    if (poolDetails.instagram) {
      formik.setFieldValue('instagram', poolDetails.instagram);
    }
    if (poolDetails.discord) {
      formik.setFieldValue('discord', poolDetails.discord);
    }
    if (poolDetails.description) {
      formik.setFieldValue('description', poolDetails.description);
    }
  }, [poolDetails]);
  return (
    <div className="py-6 container">
      <Card>
        <form onSubmit={formik.handleSubmit}>
          <div className="columns field">
            <div className="column">
              <label className="label" htmlFor="logoUrl">
                Logo URL
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames('input', formik.touched.logoUrl && formik.errors.logoUrl ? 'is-danger' : '')}
                  type="text"
                  placeholder="Ex: https://..."
                  id="logoUrl"
                  name="logoUrl"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.logoUrl}
                />
                {Boolean(formik.touched.logoUrl && formik.errors.logoUrl) && (
                  <p className="help is-danger">{formik.errors.logoUrl}</p>
                )}
                <span className="icon is-small is-left">
                  <BiImage />
                </span>
              </div>
              <p className="help is-info">URL must end with a supported image extension png, jpg, jpeg or gif</p>
            </div>
            <div className="column">
              <label className="label" htmlFor="website">
                Website
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames('input', formik.touched.website && formik.errors.website ? 'is-danger' : '')}
                  type="text"
                  placeholder="Ex: https://..."
                  id="website"
                  name="website"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.website}
                />
                {Boolean(formik.touched.website && formik.errors.website) && (
                  <p className="help is-danger">{formik.errors.website}</p>
                )}
                <span className="icon is-small is-left">
                  <FiGlobe />
                </span>
              </div>
            </div>
          </div>
          {/* */}
          <div className="columns field">
            <div className="column">
              <label className="label" htmlFor="facebook">
                Facebook
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames('input', formik.touched.facebook && formik.errors.facebook ? 'is-danger' : '')}
                  type="text"
                  placeholder="Ex: https://facebook.com/..."
                  id="facebook"
                  name="facebook"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.facebook}
                />
                {Boolean(formik.touched.facebook && formik.errors.facebook) && (
                  <p className="help is-danger">{formik.errors.facebook}</p>
                )}
                <span className="icon is-small is-left">
                  <RiFacebookCircleLine />
                </span>
              </div>
            </div>
            <div className="column">
              <label className="label" htmlFor="twitter">
                Twitter
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames('input', formik.touched.twitter && formik.errors.twitter ? 'is-danger' : '')}
                  type="text"
                  placeholder="Ex: https://twitter.com/..."
                  id="twitter"
                  name="twitter"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.twitter}
                />
                {Boolean(formik.touched.twitter && formik.errors.twitter) && (
                  <p className="help is-danger">{formik.errors.twitter}</p>
                )}
                <span className="icon is-small is-left">
                  <FiTwitter />
                </span>
              </div>
            </div>
          </div>
          {/* */}
          <div className="columns field">
            <div className="column">
              <label className="label" htmlFor="github">
                Github
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames('input', formik.touched.github && formik.errors.github ? 'is-danger' : '')}
                  type="text"
                  placeholder="Ex: https://github.com/..."
                  id="github"
                  name="github"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.github}
                />
                {Boolean(formik.touched.github && formik.errors.github) && (
                  <p className="help is-danger">{formik.errors.github}</p>
                )}
                <span className="icon is-small is-left">
                  <FiGithub />
                </span>
              </div>
            </div>
            <div className="column">
              <label className="label" htmlFor="telegram">
                Telegram
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames('input', formik.touched.telegram && formik.errors.telegram ? 'is-danger' : '')}
                  type="text"
                  placeholder="Ex: https://t.me/..."
                  id="telegram"
                  name="telegram"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.telegram}
                />
                {Boolean(formik.touched.telegram && formik.errors.telegram) && (
                  <p className="help is-danger">{formik.errors.telegram}</p>
                )}
                <span className="icon is-small is-left">
                  <FaTelegramPlane />
                </span>
              </div>
            </div>
          </div>
          {/* */}
          <div className="columns field">
            <div className="column">
              <label className="label" htmlFor="instagram">
                Instagram
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames(
                    'input',
                    formik.touched.instagram && formik.errors.instagram ? 'is-danger' : '',
                  )}
                  type="text"
                  placeholder="Ex: https://instagram.com/..."
                  id="instagram"
                  name="instagram"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.instagram}
                />
                {Boolean(formik.touched.instagram && formik.errors.instagram) && (
                  <p className="help is-danger">{formik.errors.instagram}</p>
                )}
                <span className="icon is-small is-left">
                  <AiOutlineInstagram />
                </span>
              </div>
            </div>
            <div className="column">
              <label className="label" htmlFor="discord">
                Discord
              </label>
              <div className="control has-icons-left">
                <input
                  className={classNames('input', formik.touched.discord && formik.errors.discord ? 'is-danger' : '')}
                  type="text"
                  placeholder="Ex: https://t.me/..."
                  id="discord"
                  name="discord"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.discord}
                />
                {Boolean(formik.touched.discord && formik.errors.discord) && (
                  <p className="help is-danger">{formik.errors.discord}</p>
                )}
                <span className="icon is-small is-left">
                  <FaDiscord />
                </span>
              </div>
            </div>
          </div>
          {/* Reddit */}
          <div className="field">
            <label className="label" htmlFor="reddit">
              Reddit
            </label>
            <div className="control has-icons-left">
              <input
                className={classNames('input', formik.touched.reddit && formik.errors.reddit ? 'is-danger' : '')}
                type="text"
                placeholder="Ex: https://reddit.com/..."
                id="reddit"
                name="reddit"
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                value={formik.values.reddit}
              />
              {Boolean(formik.touched.reddit && formik.errors.reddit) && (
                <p className="help is-danger">{formik.errors.reddit}</p>
              )}
              <span className="icon is-small is-left">
                <AiOutlineReddit />
              </span>
            </div>
          </div>
          {/* Description */}
          <div className="field">
            <label className="label" htmlFor="description">
              Description
            </label>
            <div className="control">
              <textarea
                className="textarea"
                placeholder="Ex: This is the best project..."
                id="description"
                name="description"
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                value={formik.values.description}
              ></textarea>
            </div>
          </div>

          <div className="has-text-centered">
            <Button type="button" onClick={goBack}>
              Back
            </Button>
            <span className="mr-4"></span>
            <Button
              loading={loading}
              type="submit"
              primary
              disabled={Boolean(formik.errors.logoUrl || formik.errors.website)}
            >
              Update
            </Button>
          </div>
        </form>
      </Card>
    </div>
  );
};

export default EditPoolPage;
