import { useMemo } from 'react';

import ChainIds from '../configs/chainIds';
import { useChainId } from '../packages/web3/hooks';

export default function useTestnet() {
  const chainId = useChainId();
  return useMemo(() => {
    return [ChainIds.BSC_TESTNET, ChainIds.MATIC_TESTNET, ChainIds.KCC_TESTNET].includes(chainId);
  }, [chainId]);
}
